.App {
  text-align: center;
}


.App-header {
  background-color: #54335f ;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 10vmin);
  color: white;
}

html {
  scroll-snap-type: y mandatory;
  background-color: #54335f ;
}

h1 {
  font-size: 56px;
  font-weight: 700;
  letter-spacing: -3px;
  line-height: 1.2;
  text-align: center;
  margin: 100px 0;
}

h2 {
  margin: 0;
  color: white;
  right: 75%;
  top: 15%;
  font-size: 56px;
  font-weight: 700;
  letter-spacing: -3px;
  line-height: 1.2;
  position: absolute;
  text-shadow:0.2em 0.1em 0.05em #777;
  writing-mode:vertical-lr;
  letter-spacing:0.25em;
}

section {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  scroll-snap-align: center;
  perspective: 500px;
}

section > div {
  width: 80vh;
  height: auto;
  position: relative;
  max-height: 85vh;
  max-width: 500px;
  margin: 20px;
  background: var(--white);
  overflow: hidden;
}

img {
  position: relative;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.progress {
  position: fixed;
  left: 0;
  right: 0;
  height: 5px;
  background: var(--accent);
  bottom: 100px;
}